/**
 * Page Title - Common
 ******************************************************************************/

.page-title-heading,
.page-title-hero {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  > .container {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  > *:not(.container),
  > .container > * {
    flex: 0 0 auto;
    &:not(:last-child) {
      margin-bottom: 0.25rem;
    }
  }

  .page-title__breadcrumbs {
    > span {
      display: inline-block;
      color: $link-color;
      font-size: 0.9375rem;
      font-weight: 600;

      a {
        color: $link-color;
      }

      &:after {
        content: '/';
        margin: 0 0.25rem;
      }
    }
  }

  .page-title__content {
    display: flex;
    justify-content: space-between;
    width: 100%;

    > h1 {
      flex: 1 1 auto;
      margin: 0;
      line-height: 1.3125;
      font-weight: 600;
      overflow-wrap: break-word;

      > small {
        display: block;
        font-size: 1.25rem;
        margin: 0.75rem 0 0.25rem 0;
      }
    }
    > .page-title__actions {
      flex: 1 0 auto;
      display: flex;
      align-self: flex-start;
      justify-content: flex-end;
      margin-left: auto;
      padding-left: 1rem;

      > * {
        margin: 0.1875rem 0 0.1875rem 0.375rem;
        white-space: nowrap;
        &:first-child {
          margin-left: 0;
        }
      }

      .dropdown-menu {
        font-size: 0.875rem;
      }
    }
    @media all and (max-width: 991px) {
      flex-wrap: wrap;
      > h1 + .page-title__actions {
        flex: 1 0 100%;
        margin-top: 0.5rem;
      }
      .page-title__actions {
        justify-content: flex-start;
        padding-left: 0;

        .dropdown {
          margin-left: auto;
        }
      }
    }
  }

  .page-title__meta {
    font-size: 0.8125rem;
    color: $gray-600;
    margin-top: 0.25rem;

    dl {
      margin-bottom: 0;
      div {
        display: inline-block;
        white-space: nowrap;
        &:not(:last-child):after {
          content: '\2022';
          margin: 0 0.625rem;
        }
      }
      dt {
        display: inline-block;
        font-weight: 500;
        &:after {
          content: ': ';
          margin-right: 0.25rem;
        }
      }
      dd {
        display: initial;
        font-weight: 600;
        margin-bottom: 0.125rem;

        a {
          color: inherit;
          font-size: inherit;
        }
      }
      .badge {
        font-size: 0.8125rem;
      }
    }
  }
}

.page-title-hero {
  padding: 3rem 2rem;
  margin: 2.5rem 0 3rem;
  background-color: $hero-bg;
  background-position: center;
  background-size: cover;
  color: $hero-color;
  min-height: 12rem;
  border-radius: $border-radius-lg;
  box-shadow: 0 0.125rem 0.75rem rgba(0, 0, 0, 0.15);

  &--center {
    justify-content: center;
  }

  &--image {
    z-index: 0;
    &:before {
      content: ' ';
      position: absolute;
      background-color: $hero-overlay;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: -1;
      border-radius: $border-radius;
    }
  }

  + .search-box {
    &:not(.container) {
      padding: 0 1.5rem;
    }

    position: relative;
    top: -1rem;
  }

  .page-title__breadcrumbs {
    > span {
      color: $hero-meta-color;

      a {
        color: $hero-meta-color;
      }
    }
  }

  .page-title__meta {
    color: $hero-meta-color;
  }

  &--with-card {
    padding-right: 26rem;
    margin-bottom: 5rem;
    min-height: 17rem;

    .page-title__card {
      position: absolute;
      right: 3rem;
      height: calc(100% - 1rem);
      width: 21rem;
      display: flex;
      bottom: -2rem;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      > .card {
        box-shadow: 0 0.125rem 0.75rem rgba(0, 0, 0, 0.15);
        overflow: auto;
        flex: 1 1 auto;

        + .card {
          margin-top: 1.5rem;
        }
      }
    }

    @media all and (max-width: 991px) {
      padding-right: 2rem;

      .page-title__card {
        position: relative;
        height: auto;
        width: 100%;
        right: auto;
        bottom: -5rem;
        margin-top: -3rem;
      }
    }
  }
}

.page-title-heading {
  padding: 2.5rem 0 3rem 0;

  &--padding {
    padding: 1.5rem;
  }
}

// Old Page Title styling

.page-title {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
  flex: 1 1 100%;

  h1,
  .breadcrumb-last {
    margin-bottom: 1rem;
    color: $blue-slate;
    font-size: 2rem;
    font-weight: 300;
    line-height: 1.25;
    &:first-child {
      margin-top: 1.5rem;
    }
    small,
    .small {
      font-size: 1.6rem;
      font-weight: 300;
    }
  }

  ol,
  .page-title__breadcrumbs {
    list-style: none;
    padding: 0;
    margin: 1.5rem 0 0;

    .breadcrumb-item {
      display: inline-block;
      color: darken($orange-kinops, 15.5);
      font-size: 0.9375rem;
      font-weight: 700;
      letter-spacing: 1px;
      line-height: 1.5rem;
      + span,
      a {
        color: darken($orange-kinops, 15.5);
        font-size: 0.9375rem;
        font-weight: 700;
        letter-spacing: 1px;
        line-height: 1.5rem;
      }
    }
  }

  .page-title__actions {
    display: flex;
    align-self: flex-end;
    justify-content: flex-end;
    flex-wrap: wrap;
    margin-bottom: 1rem;
    > .btn,
    > .btn-group,
    > .file-up {
      margin: 0.1rem 0 0.1rem 0.2rem;
    }
    .btn {
      white-space: nowrap;
    }
    @media all and (max-width: 767px) {
      align-self: flex-start;
      justify-content: flex-start;
      margin-left: -0.2rem;
    }
  }

  // Any object to the right of the title
  > a,
  > button,
  > .btn-group,
  > .page-title__actions {
    margin-bottom: 1rem;
    @media all and (min-width: 768px) {
      align-self: flex-end;
    }
  }

  @media all and (max-width: 767px) {
    flex-direction: column;
  }
}

// Section Title with top bar. - TODO move to _layout?
.section__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  margin: 0 0 1.5rem 0;
  border-bottom: 1px solid $border-color;

  .title {
    font-size: 1.5rem;
    padding: 0.25rem 1rem;
    border-bottom: 2px solid $secondary;
    margin-bottom: -1px;
  }

  &--sm {
    .title {
      font-size: 1rem;
    }
  }

  a,
  .btn {
  }

  .dropup,
  .dropright,
  .dropdown,
  .dropleft {
    position: initial;
  }

  + .section__content {
    margin-bottom: 3rem;
    > table:first-child {
      margin-top: -0.5rem;
      th {
        border-top: none;
      }
    }
  }
}
