/**
 * Layout - Common
 ******************************************************************************/

.package-layout {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;

  .page-container {
    position: relative;
    flex: 1;
    min-height: auto;
    width: 100%;
    margin: 0 auto;

    &--lg {
      max-width: 85rem;
    }
    &--xl {
      max-width: 100rem;
    }

    &--panels {
      display: flex;
      height: 100%;
      max-width: none;
      .page-panel {
        overflow: auto;
        &:not(:last-child) {
          // border-right: 1px solid $border-color;
        }
      }
    }

    .page-panel {
      $paddingX: (
        sm: 1.5rem,
        md: 2.25rem,
        lg: 3rem,
        xl: 3.5rem,
      );
      $paddingY: (
        sm: 1.5rem,
        md: 1.875rem,
        lg: 2.25rem,
        xl: 2.5rem,
      );
      position: relative;
      padding: 0 3.5rem;
      @media all and (max-width: 1399px) {
        padding: 0 3rem;
      }
      @media all and (max-width: 1199px) {
        padding: 0 2.25rem;
      }
      @media all and (max-width: 991px) {
        padding: 0 1.5rem;
      }
      width: 100%;
      // min-height: 100%;
      .page-panel__header,
      .page-panel__footer {
        margin: 0 -3.5rem;
        @media all and (max-width: 1399px) {
          margin: 0 -3rem;
        }
        @media all and (max-width: 1199px) {
          margin: 0 -2.25rem;
        }
        @media all and (max-width: 991px) {
          margin: 0 -1.5rem;
        }
      }
      .page-panel__body {
        padding-top: 2.5rem;
        padding-bottom: 2.5rem;
        @media all and (max-width: 1399px) {
          padding-top: 2.25rem;
          padding-bottom: 2.25rem;
        }
        @media all and (max-width: 1199px) {
          padding-top: 1.875rem;
          padding-bottom: 1.875rem;
        }
        @media all and (max-width: 991px) {
          padding-top: 1.5rem;
          padding-bottom: 1.5rem;
        }
        &.container {
          padding-left: 0;
          padding-right: 0;
        }
      }
      &--sidebar {
        @media (max-width: 575px) {
          display: none;
        }
      }
      &--flex {
        display: flex;
        flex-direction: column;
        padding: 0;
        height: 100%;
        .page-panel__header,
        .page-panel__footer {
          margin: 0;
          flex: 0 0 auto;
          min-height: 0px;
          &--padding-x {
            padding-left: 3.5rem;
            padding-right: 3.5rem;
            @media all and (max-width: 1399px) {
              padding-left: 3rem;
              padding-right: 3rem;
            }
            @media all and (max-width: 1199px) {
              padding-left: 2.25rem;
              padding-right: 2.25rem;
            }
            @media all and (max-width: 991px) {
              padding-left: 1.5rem;
              padding-right: 1.5rem;
            }
          }
          &--padding-y {
            padding-top: 2.5rem;
            padding-bottom: 2.5rem;
            @media all and (max-width: 1399px) {
              padding-top: 2.25rem;
              padding-bottom: 2.25rem;
            }
            @media all and (max-width: 1199px) {
              padding-top: 1.875rem;
              padding-bottom: 1.875rem;
            }
            @media all and (max-width: 991px) {
              padding-top: 1.5rem;
              padding-bottom: 1.5rem;
            }
          }
        }
        .page-panel__body {
          padding: 2.5rem 3.5rem;
          @media all and (max-width: 1399px) {
            padding: 2.25rem 3rem;
          }
          @media all and (max-width: 1199px) {
            padding: 1.875rem 2.25rem;
          }
          @media all and (max-width: 991px) {
            padding: 1.5rem;
          }
          flex: 1 1 0px;
          min-height: 0px;
          overflow: auto;
        }
      }
      &--padding-y {
        padding-top: 2.5rem;
        padding-bottom: 2.5rem;
        @media all and (max-width: 1399px) {
          padding-top: 2.25rem;
          padding-bottom: 2.25rem;
        }
        @media all and (max-width: 1199px) {
          padding-top: 1.875rem;
          padding-bottom: 1.875rem;
        }
        @media all and (max-width: 991px) {
          padding-top: 1.5rem;
          padding-bottom: 1.5rem;
        }
      }
      &--no-padding {
        padding: 0;
        .page-panel__header,
        .page-panel__footer {
          margin: 0;
        }
        .page-panel__body {
          padding: 0;
        }
      }
      // &--scrollable {
      // overflow-y: auto;
      // }
      &--exact {
        flex: 0 0 auto;
        width: auto;
      }
      // Thirds
      &--two-thirds {
        flex: 1 1 66.66666666%;
      }
      &--one-thirds,
      &--one-third {
        flex: 1 1 33.33333333%;
      }
      // Fifths
      &--three-fifths {
        flex: 1 1 60%;
      }
      &--two-fifths {
        flex: 1 1 40%;
      }
      .column-container {
        display: flex;
        margin: 0 -1.5rem;
        .column-panel {
          flex: 1 1 50%;
          padding: 0 1.5rem;
          // Thirds
          &--thirds {
            flex-basis: 33.33333333%;
          }
          &--two-thirds {
            flex-basis: 66.66666666%;
          }
          &--one-third,
          &--one-thirds {
            flex-basis: 33.33333333%;
          }
          &--one-quarter {
            flex-basis: 25%;
          }
          &--three-quarters {
            flex-basis: 75%;
          }
        }
        @media (max-width: 991px) {
          flex-direction: column;
          .column-panel {
            flex-basis: auto;
          }
        }
      }
    }
    .page-panel--sidebar {
      // Reducted sidebar typography
      h3 {
        font-size: 1.125rem;
      }
      h4 {
        font-size: 1rem;
        margin-bottom: 0.25rem;
      }
      p {
        font-size: 0.875rem;
      }
    }
  }
}
.data-list-row {
  display: flex;
  flex-wrap: wrap;
  &__col {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 1rem;
    padding-left: 1rem;
  }
  dl {
    color: $gray-800;
    font-size: 0.875rem;
    margin-bottom: 0;
  }
}
.group-divider {
  align-items: center;
  color: $primary;
  display: flex;
  font-size: 0.875rem;
  font-weight: bold;
  text-align: center;
  > hr {
    border: 2px solid $tertiary;
    flex: 1 0 auto;
  }
  > span {
    margin: 0 0.75rem;
    &:empty {
      display: none;
    }
  }
}
