/**
 * Pagination - Common
 ******************************************************************************/

.pagination-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 1rem;
}

.page-item.active .page-link {
  z-index: 1;
  color: $white;
  background-color: $blue-lake;
  border-color: $blue-lake;
  &:hover {
    color: $white;
    background-color: $blue-lake;
  }
}
.page-link {
  color: $blue-lake;
  background-color: $white;
  border-color: $gray10;
  &:hover {
    border-color: $gray16;
    // background-color: $white;
    box-shadow: 0 1px 2px 0 rgba($gray80, 0.1);
  }
}
