/**
 * Badges - Common
 ******************************************************************************/

@each $color, $value in $theme-colors {
  .badge-#{$color} {
    &.badge-muted {
      color: theme-color-level($color, $badge-muted-color-level);
      background-color: theme-color-level($color, $badge-muted-bg-level);

      @at-root a#{&} {
        @include hover-focus() {
          background-color: darken(
            theme-color-level($color, $badge-muted-bg-level),
            10%
          );
        }
      }
    }

    &.badge-stylized {
      background-color: transparent;
      color: $body-color;
      padding: 0;
      font-size: 0.8em;
      line-height: 1.5;
      &:before {
        content: '\f0c8';
        font-family: 'FontAwesome';
        color: $value;
        font-size: 0.9em;
        line-height: inherit;
        margin-right: 0.375rem;
      }
    }
  }
}
