/**
 * Discussion - Common
 ******************************************************************************/

// Discussion container fixes
.page-panel--discussions {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  position: relative;
  padding: 0;
  overflow-y: auto;
}

.discussion {
  background-color: $gray3;
  display: flex;
  flex-direction: column;
  flex: 1;
  position: relative;

  &--list {
    &__header {
      &-content {
        padding: 1.5rem;
      }
    }
  }

  &__header {
    background-color: $white;
    border-bottom: 1px solid $gray16;
    font-size: 0.9375rem;
    font-weight: 600;
    letter-spacing: 1px;
    padding: 0.75rem 1.5rem;
    width: 100%;
    color: darken($orange-kinops, 14);
    h1,
    h2 {
      margin: 0;
      padding: 0;
      font-size: 0.9375rem;
      font-weight: 600;
      line-height: 1.5;
      letter-spacing: 1px;
    }
    a:not(.related-link) {
      color: darken($orange-kinops, 14);
      font-weight: 600;
    }
  }

  &__subheader {
    display: flex;
    justify-content: flex-end;
    border-bottom: 1px solid $gray16;

    .btn {
      padding: 0.75rem 0.75rem;
      border-radius: 0;
      border: none;
      flex: 0 0 auto;
      &:first-child {
        padding-left: 1.5rem;
      }
      &:last-child {
        padding-right: 1.5rem;
      }
      &:not(:hover) {
        color: $gray60;
      }
    }

    .btn-back {
      flex: 1;
      text-align: left;
    }

    .btn-icon {
      .icon {
        position: relative;
        .badge {
          position: absolute;
          top: -6px;
          left: 10px;
          font-size: 0.625rem;
          padding: 0.15rem;
          border-radius: 50%;
          background-color: $orange-kinops;
        }
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    padding: 0 !important;
    flex-grow: 1;
    position: relative;

    .discussion__messages {
      background-color: $gray3;
      flex: 1;
      position: relative;
    }

    .discussion__meta {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 3rem;
      display: flex;
      align-items: center;
      background-color: rgba($white, 0.8);
      padding: 0 1.5rem;
      z-index: 0;
    }

    .discussion__dropzone {
      .markdown-help {
        overflow: hidden;
      }
      input[type='file'] {
        max-width: 100%;
      }
    }

    .discussion__summaries {
      padding: 0 1.5rem;

      .discussion__summary {
        display: flex;
        flex-direction: column;
        border: 1px solid $gray6;
        border-radius: $border-radius;
        background-color: $white;
        padding: 0.5rem 1rem 0.5rem 1rem;
        margin-bottom: 0.75rem;
        &:last-child {
          margin-bottom: 0;
        }

        .header {
          display: flex;
          width: 100%;
          justify-content: space-between;
          margin-bottom: 1rem;
          align-items: center;

          &:last-child {
            margin-bottom: 0;
          }

          &__title {
            margin-right: 0.25em;
            // height: 1.5rem;
            font-size: 1rem;
            font-weight: 600;
            line-height: 1.5rem;
            color: $black;
            // overflow: hidden;
            // text-overflow: ellipsis;
            // white-space: nowrap;
            word-break: break-word;
            text-align: left;
            flex: 1 1 auto;
            cursor: pointer;
          }

          .participants {
            flex: 1 0 auto;
            max-width: 40%;
          }
        }
        .messages .messages__grouping:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  .banner--archived {
    position: absolute;
    top: 3rem;
    left: 0;
    right: 0;
    height: 3rem;
    background-color: rgba($gray2, 0.85);
    padding: 0 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    .message {
      margin: 0 0.5rem;
      font-weight: 600;
    }
    &.text-danger {
      color: $danger70 !important;
    }
    a:hover {
      cursor: pointer;
      text-decoration: underline;
    }
  }
}

// People and Team selectes
.user-menu-item,
.team-menu-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.date-divider {
  margin: 1rem 0;

  > hr {
    margin: 0;
    border: none;
    height: 2px;
    background: linear-gradient(to left, $subtle, transparent);
  }

  > span {
    position: relative;
    color: $black;
    background-color: $subtle;
    margin: 0;
    font-size: 0.75rem;
    padding: 0.125rem 0.5rem;
    border-radius: 3px;
    height: 1.375rem;

    &:before {
      content: '';
      position: absolute;
      height: 1.375rem;
      width: 1.375rem;
      left: -1.3125rem;
      top: 0;
      border: 0.6875rem solid transparent;
      border-right-color: $subtle;
    }

    + hr {
      display: none;
    }
  }
}

.discussions-container {
  .discussion {
    background-color: transparent;

    &__content {
      .discussion__summaries {
        padding: 0;

        .discussion__summary {
          border: none;
          background-color: $gray-100;
          padding: 0.75rem 1.5rem;

          .header {
            margin-bottom: 0;
          }
          .messages {
            .messages__grouping {
              .message-list {
                &__item:hover {
                  background-color: transparent;
                }
              }

              .message-bubble {
                background-color: $gray-300;
                &:after {
                  border-color: $gray-300 $gray-300 transparent transparent;
                }
              }

              &.is-mine {
                .message-bubble {
                  background-color: mix($gray-300, $cyan, 90%);
                  &:after {
                    border-color: mix($gray-300, $cyan, 90%) transparent
                      transparent mix($gray-300, $cyan, 90%);
                  }
                }
              }
            }
          }
        }
      }

      .discussion__messages {
        min-height: 25vh;
      }
    }
  }
  .date-divider {
    padding: 0;
  }
}
