/**
 * Signature - Common
 ******************************************************************************/

.signature-canvas {
  position: relative;
  display: flex;
  width: 100%;
  canvas {
    position: relative;
    background-color: $gray-100;
    width: 100%;
    height: auto;
  }
  &:after {
    content: 'X';
    position: absolute;
    bottom: 0;
    width: 90%;
    font-family: $font-family-monospace;
    font-size: 1.5rem;
    text-align: left;
    flex: 1 1 auto;
    color: $gray-400;
    padding: 0.25rem 0.75rem;
    margin: 10% 5%;
    border-bottom: 1px solid $gray-400;
    pointer-events: none;
  }

  &.signed {
    pointer-events: none;
  }
}

.signature-button {
  display: flex;
  align-items: stretch;
  height: 5rem;
  max-width: 100%;
  border: 2px solid $warning;
  border-radius: $border-radius;
  background-color: $gray-100;
  padding: 0;
  box-sizing: content-box;
  .title {
    position: relative;
    flex: 0 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $warning;
    color: color-yiq($warning);
    padding: 0.5rem;
    margin-right: 2.5rem;
    box-sizing: border-box;
    > * {
      flex: 1 0 auto;
    }
    &:after {
      content: ' ';
      position: absolute;
      right: -5rem;
      height: 100%;
      width: 5rem;
      border: 2.5rem solid transparent;
      border-left: 2.5rem solid $warning;
      pointer-events: none;
    }
  }
  .signature {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 8rem;
    max-width: 20rem;
    padding: 0.5rem;
    img {
      max-height: 4rem;
    }
  }

  &:not(.signed) {
    .signature {
      align-items: flex-end;
      &:after {
        content: 'X';
        font-family: $font-family-monospace;
        font-size: 1rem;
        text-align: left;
        flex: 1 1 auto;
        color: $gray-400;
        padding: 0.25rem 0.75rem;
        margin: 0.5rem;
        border-bottom: 1px solid $gray-400;
      }
    }
  }
  &.signed {
    border-color: $success;
    .title {
      background-color: $success;
      color: color-yiq($success);
      &:after {
        border-left-color: $success;
      }
    }
  }
}
