/**
 * Sidebar - Common
 ******************************************************************************/

 .sidebar {
   background-color: $gray-100;
   border-right: 1px solid $border-color;
   min-height: calc(100vh - 160px);
   height: 100%;
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   width: 16rem;
   overflow: hidden;
   position: relative;

   // Grouping of related links
   .sidebar-group {
     padding: 0.75rem 0 0 0;
     list-style: none;
     z-index: 0;

     // Group Title
     h1,
     &__label {
       color: $primary;
       display: flex;
       align-items: center;
       line-height: 1;
       font-size: 0.75rem;
       padding: 0.75rem 1.5rem;
       margin: 0;
       text-transform: uppercase;
       justify-content: space-between;

       // Group Title Link or action
       a {
         line-height: normal;
         margin-left: auto;
         text-transform: none;
         color: $gray-600;
         &:hover {
           color: $primary;
         }
       }
       // Group Title Button color
       .btn-icon {
         padding: 0;
         margin: 0;
       }
     }

     .nav-link {
       border-left: 3px solid transparent;
       color: $gray-600;
       line-height: normal;
       display: flex;
       align-items: center;
       margin: 0 0 0.75rem 0;
       font-size: 0.875rem;
       padding: 0.25rem 1.5rem;
       transition: border-left-color 0.5s, color 0.5s;
       .fa {
         font-size: 1rem;
         margin-right: 0.5rem;
       }
       &.active,
       &:hover {
         border-left-color: $primary;
         color: $primary;
       }
     }

     .empty-state {
       padding: 0.25rem 1.5rem;
       font-size: 0.875rem;
       font-style: italic;
       color: rgba($gray-800, 0.75);
       margin-bottom: 0.75rem;
     }

     &--content-wrapper {
       overflow: auto;
     }

     &--settings {
       // position: fixed;
       // bottom: 0;
       // left: 0;
       width: 100%;
       max-width: 16rem;
       padding: 0;
       margin-top: auto;
       background-color: darken($gray-100, 3%);
       z-index: 10;
       .nav-link {
         display: flex;
         justify-content: space-between;
         align-items: center;
         padding: 0.75rem 1.5rem;
         margin: 0;
         font-weight: 600;
         border-left: none;
         .fa {
           margin-right: 0;
         }
         &:hover {
           border-left: none;
         }
       }
     }
   }
 }
