/**
 * Modals - Common
 ******************************************************************************/

@media (max-width: 767px) {
  .modal-dialog {
    height: 100%;
    margin: 0;
    max-width: none;
    .modal-content {
      border: none;
      display: flex;
      flex-direction: column;
      height: 100%;
      .modal-body {
        // Flexbugs #4: https://github.com/philipwalton/flexbugs#4-flex-shorthand-declarations-with-unitless-flex-basis-values-are-ignored
        flex: 1 0 0%;
        flex-grow: 1;
        flex-shrink: 0;
        flex-basis: 0;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
      }
    }
  }
}

@media (min-width: 768px) {
  .modal-dialog {
    .modal-content {
      border: none;
      box-shadow: 0 2px 14px 0 rgba(0, 0, 0, 0.5);
    }
  }
}

.modal-dialog {
  .modal-content {
    border-radius: 0;
    .modal-header {
      flex-direction: column;
      align-items: stretch;
      padding: 0;
      .modal-title {
        display: flex;
        justify-content: center;
        position: relative;
        min-height: 3rem;
        font-size: 1rem;
        font-weight: bold;
        span {
          padding: 0.75rem 6rem;
          text-align: center;
        }
        a,
        button {
          position: absolute;
          top: 0;
          bottom: 0;
          padding: 0 1rem;
          &:first-child {
            left: 0;
          }
          &:last-child {
            right: 0;
          }
        }
        a {
          display: flex;
          align-items: center;
        }
      }
      .btn.btn-back {
        border-top: 1px solid $border-color;
        border-bottom: 0;
        padding: 0.8125rem 1rem;
        text-align: left;
        .fa {
          margin-right: 0.25rem;
        }
      }
    }
    .modal-body {
      &--padding,
      &.form {
        padding: 1rem;
      }
      display: flex;
      flex-direction: column;
      padding: 0;
      > .embedded-core-form,
      > .modal-form,
      > .modal-confirmation {
        padding: 1rem;
      }
      > .modal-confirmation {
        p {
          margin: 0;
        }
      }
      .embedded-core-form .container,
      form {
        padding: 0;
        overflow: visible;
      }
      .list-group.button-list {
        .list-group-item {
          border-bottom: 1px solid $border-color;
          padding: 0;
          .btn.btn-link {
            font-size: 0.875rem;
            padding: 0.875rem 1rem;
            flex-grow: 1;
            display: flex;
            .button-title {
              font-weight: 600;
              margin-right: auto;
            }
          }
        }
      }
      .button-list .btn-link {
        .validation-error {
          max-width: 200px;
          text-align: right;
          white-space: normal;
        }
      }
      .save-filter {
        display: flex;
        flex-direction: column;
        margin: 1rem;
        label {
          color: $primary;
          font-size: 0.75rem;
          line-height: 1.5rem;
        }
        input {
          min-height: 1.5rem;
          border: 1px solid $gray16;
          border-radius: $border-radius;
          background-color: $white;
          padding: 0.5em;
          color: $gray80;
          font-size: 0.75rem;
          line-height: 1.5rem;
        }
        .btn {
          margin: 1rem 0;
          + .btn {
            margin-top: 0;
          }
        }
      }
      &.filter-section {
        padding-bottom: 3rem;
        h5 {
          font-weight: 600;
          color: $blue-slate;
          font-size: 1rem;
          padding: 1rem;
          margin: 0;
          ~ h5 {
            margin-top: 1rem;
          }
        }
        label {
          border-bottom: 1px solid rgba(244, 246, 249, 1);
          color: $blue-lake;
          font-size: 0.875rem;
          margin-bottom: 0;
          padding: 1rem;
          + small {
            padding: 1rem;
            border-bottom: 1px solid rgba(244, 246, 249, 1);
          }
        }
        input[type='checkbox'],
        input[type='radio'] {
          margin-right: 0.75rem;
        }
        select,
        input[type='text'] {
          margin: 0 1rem 1rem;
          width: calc(100% - 2rem);
        }
        .date-range-date {
          display: flex;
          align-items: center;
          padding: 1rem;
          border-bottom: 1px solid rgba(244, 246, 249, 1);
          label {
            border-bottom: none;
            padding: 0;
            width: 10rem;
          }
        }
      }
      &--import-export {
        h2 {
          font-size: 1.125rem;
          margin: 1rem 0 0;
        }
        h3 {
          font-size: 1rem;
          margin: 1rem 0 0;
        }
        h4 {
          font-size: 0.875rem;
          margin: 0.25rem 0 1rem;
        }
      }
    }
    .modal-footer {
      &:not(.modal-footer--padding) {
        padding: 0;

        .btn {
          flex-grow: 1;
          border-radius: 0;
          padding: 1rem;
          margin: 0;
        }
      }
    }

    > form {
      padding: 0;
    }
  }
}

/* Styles for modal built into core */
.kd-modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 2048;
  background-color: rgba(0, 0, 0, 0.5);

  .kd-modal-content {
    position: relative;
    background-color: #fefefe;
    margin: 1.75rem auto;
    padding: 0;
    max-width: 820px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    overflow: hidden;
    border: none;
    border-radius: 0;

    .kd-modal-header {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding: 0.75rem 1rem;
      background-color: $white;
      color: $white;
      border-radius: 0;
      font-size: 1.5rem;
      line-height: 1.875rem;
      border-bottom: 1px solid $border-color;
      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
        color: $body-color;
        padding: 0;
        margin: 0;
        display: inline-block;
      }
      .fa {
        font-size: 1.25rem;
        line-height: 1.875rem;
        cursor: pointer;
      }
      a,
      button {
        color: $white;
        &:hover,
        &:focus {
          color: rgba($white, 0.85);
        }
      }
      button.close {
        font-size: 1.25rem;
        line-height: 1.875rem;
        cursor: pointer;
        opacity: 1;
        span {
          display: none;
        }
        &:before {
          content: '\F00D';
          font-family: FontAwesome;
          font-weight: normal;
        }
      }
      > [data-element-type='content'] {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex: 1;
      }
      h4.modal-title {
        order: -1;
        color: $white;
        font-size: 1.5rem;
        line-height: 1.875rem;
        font-weight: 400;
        font-family: $font-family-base;
        &:only-child {
          flex: 1 1 auto;
          text-align: center;
        }
      }
    }
    .kd-modal-body {
      padding: 1rem;
      max-height: 75vh;
      overflow: auto;
      .container {
        width: auto;
      }
      table {
        width: 100%;
        th,
        td {
          padding: 0.5rem;
        }
        tbody tr {
          cursor: pointer;
          &:hover {
            background-color: $gray-200;
          }
        }
      }

      hr {
        border-color: #eee;
      }

      .embedded-core-form form {
        margin: 0;
        padding: 0;
        background-color: transparent;
        box-shadow: none;
      }
    }

    .kd-modal-footer {
      padding: 0.3125rem 1rem;
      background-color: $gray-100;
      color: $white;
      border-top: 1px solid $border-color;
      .btn {
        &.btn-primary,
        &.btn-secondary,
        &.btn-danger {
          flex-grow: initial;
          border-radius: 0.25rem;
          padding: 0.5rem 0.5rem;
        }
      }
      &:empty {
        display: none;
      }
    }
  }
}

@media (max-width: 767px) {
  .kd-modal-content {
    width: 90% !important;
  }
}
