// Scrollbar Styling
@mixin scrollbar($color: rgb(137, 139, 142), $bgColor: rgb(241, 241, 243)) {
  // Firefox styling
  scrollbar-width: thin;
  scrollbar-color: $color $bgColor;
  // Chrome/Edge styling
  &::-webkit-scrollbar {
    width: 10px;
  }
  &::-webkit-scrollbar-track {
    background: $bgColor;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $color;
  }
}
