/**
 * Buttons - Common
 ******************************************************************************/

// Muted variant for link button
.btn-link-muted {
  @extend .btn-link;
  color: $text-muted;

  @include hover() {
    color: darken($text-muted, 5%);
  }
}

// Danger variant for link button
.btn-link-danger {
  @extend .btn-link;
  color: darken($danger, 14%);

  @include hover() {
    color: darken($danger, 19%);
  }
}

// Override button-outline-danger with darker red to support gray background
.btn-outline-danger {
  @include button-outline-variant(
    darken($danger, 16%),
    color-yiq($danger),
    $danger,
    $danger
  );
}

// Make a button look and behave like a link in text
.btn-text {
  @extend .btn-link;
  font: inherit;
  line-height: inherit;
  vertical-align: inherit;
  padding: 0;
  border: 0;

  &-danger {
    @extend .btn-text;
    color: darken($danger, 14%);

    @include hover() {
      color: darken($danger, 19%);
    }
  }
}

// Button with gray border and white background
.btn-white,
.btn-inverse {
  @include button-variant($white, $border-color);
}

// Buttons with icons
.btn-icon {
  font-weight: $font-weight-normal;
  color: $gray-600;

  @include hover() {
    color: $primary;
  }

  &:disabled,
  &.disabled {
    pointer-events: none;
  }

  // Primary variant
  &-primary {
    color: $primary;

    @include hover() {
      color: darken($primary, 10%);
    }
  }

  // Danger variant
  &-danger {
    color: darken($danger, 14%);

    @include hover() {
      color: darken($danger, 19%);
    }
  }

  // Light variant
  &-light {
    color: $gray-100;

    @include hover() {
      color: darken($gray-100, 5%);
    }
  }

  // Subtle variant
  &-subtle {
    color: $gray-600;

    @include hover() {
      color: darken($gray-600, 5%);
    }
  }

  // Dark variant
  &-dark {
    color: $gray-800;

    @include hover() {
      color: darken($gray-800, 5%);
    }
  }

  .icon,
  .fa,
  &-primary .fa,
  &-danger .fa,
  &-light .fa,
  &-subtle .fa,
  &-dark .fa {
    font-size: 1.125rem;
    line-height: 1.33333333;
  }
}

.btn-sticky {
  &-top {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    margin-top: -1px;
  }
  &-bottom {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    margin-bottom: -1px;
  }
}

// TODO - increase default btn-sm to 30px and make btn-xs be 24px?
.btn-xs {
  @extend .btn-sm;
}

// Kinetic form buttons
[data-element-type='button']:not(.btn) {
  @extend .btn;
  &[data-button-type='submit-page'],
  &[data-button-type='custom'] {
    @extend .btn-primary;
  }
  &[data-button-type='previous-page'],
  &[data-button-type='save'] {
    @extend .btn-subtle;
  }
}

// Kinetic form file input button
.fileinput-button {
  @extend .btn-subtle;
}
