/**
 * Variables - Common
 *
 * This bundle uses Bootstrap styling, and the below variables are overrides
 * for the defualt Bootstrap variables.
 *
 * Any new variables are denoted with a '// Custom' comment.
 *
 * You can also override any other variables defined by Bootstrap found here:
 *   https://github.com/twbs/bootstrap/blob/main/scss/_variables.scss
 ******************************************************************************/

/** Modularscale Font Sizing **************************************************/
$modularscale: (
  768px: (
    base: 14px,
    ratio: 1.125,
  ),
  1200px: (
    base: 16px,
    ratio: 1.125,
  ),
) !default;

/** Color system **************************************************************/

/**-- Grays ------------------------------------------------------------------*/
$white:     rgb(255, 255, 255) !default;  // #ffffff
$gray-100:  rgb(246, 247, 248) !default;  // #f6f7f8 (replaces $gray3)
$gray-200:  rgb(238, 239, 240) !default;  // #eeeff0 (replaces $gray8)
$gray-300:  rgb(228, 229, 230) !default;  // #e4e5e6 (replaces $gray10)
$gray-400:  rgb(212, 213, 214) !default;  // #d4d5d6 (replaces $gray16)
$gray-500:  rgb(178, 179, 181) !default;  // #b2b3b5 (replaces $gray30)
$gray-600:  rgb(109, 111, 113) !default;  // #6d6f71 (replaces $gray50/60)
$gray-700:  rgb(79, 80, 81) !default;     // #4f5051 (replaces $gray70)
$gray-800:  rgb(54, 55, 56) !default;     // #363738 (replaces $gray80)
$gray-900:  rgb(27, 28, 29) !default;     // #1B1C1D (replaces $gray90)
$black:     rgb(0, 0, 0) !default;        // #000000

/**-- Colors -----------------------------------------------------------------*/
$blue:           rgb(20, 116, 229) !default;    // #1474e5
$blue-gray:      rgb(168, 183, 199) !default;  // #a8b7c7 - Custom
$blue-lake:      rgb(9, 84, 130) !default;     // #095482 - Custom
$blue-slate:     rgb(12, 56, 79) !default;     // #0c384f - Custom
$blue-sky:       rgb(11, 168, 224) !default;   // #0ba8e0 - Custom / deprecated
$cyan:           rgb(11, 168, 224) !default;   // #0ba8e0
$green:          rgb(27, 134, 99) !default;    // #00d46a // #1b8663
$green-grass:    rgb(0, 212, 106) !default;    // #00d46a - Custom / depecated
$green-lime:     rgb(102, 225, 65) !default;   // #66e141 - Custom
$green-teal:     rgb(2, 212, 177) !default;    // #02d4b1 - Custom
// $indigo                                     // not overwritten
$indigo-dark:    rgb(75, 0, 130) !default;     // #4b0082 - Custom
$orange:         rgb(255, 153, 28) !default;   // #ff991c
$orange-kinops:  rgb(243, 108, 36) !default;   // #f36c24 - Custom
// $pink                                       // not overwritten
// $purple                                     // not overwritten
$red:            rgb(207, 69, 32) !default;    // #cf4520
$red-purple:     rgb(191, 52, 121) !default;   // #bf3479 - Custom
$red-rose:       rgb(255, 74, 94) !default;    // #ff4a5e - Custom
$red-violet:     rgb(166, 48, 150) !default;   // #a63096 - Custom
$sunflower:      rgb(255, 207, 74) !default;   // #ffcf4a - Custom
// $teal                                       // not overwritten
$yellow:         rgb(254, 233, 78) !default;   // #fee94e

/**-- KD Colors Map - Custom -------------------------------------------------*/
$kd-colors: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$kd-colors: map-merge(
  (
    'blue':         $blue,
    'blue-gray':    $blue-gray,
    'blue-lake':    $blue-lake,
    'blue-slate':   $blue-slate,
    'cyan':         $cyan,
    'green':        $green,
    'green-lime':   $green-lime,
    'green-teal':   $green-teal,
    'indigo-dark':  $indigo-dark,
    'orange':       $orange,
    'red':          $red,
    'red-violet':   $red-violet,
    'red-purple':   $red-purple,
    'red-rose':     $red-rose,
    'sunflower':    $sunflower,
    'yellow':       $yellow,
  ),
  $kd-colors
);

/**-- Theme Colors -----------------------------------------------------------*/
$primary:    $blue-lake !default;
$secondary:  $orange-kinops !default;
$tertiary:   #0098CE !default;
$success:    $green !default;
$info:       $blue !default;
$warning:    $sunflower !default;
$danger:     $red !default;
$light:      $gray-100 !default;
$subtle:     $gray-400 !default;
$dark:       $gray-800 !default;

$theme-colors: () !default;
$theme-colors:  map-merge(
  (
    'tertiary':  $tertiary,
    'subtle':    $subtle,
  ),
  $theme-colors
);

/**-- Complement Variables -----------------------------------------------------
 * The default bootstrap color-yiq function has been overriden to use color
 * contrast based on luminance instead of the yiq color space, to support
 * sufficient a11y contrast. You may disable the overriden function below.
 *----------------------------------------------------------------------------*/
$enable-yiq-a11y-override:  true !default; // Custom
// Contrast threshold for picking light vs dark text colors using a11y override
$a11y-contrast-threshold:   4.5 !default;
// Contrast treshold for default bootstrap color-yiq function
$yiq-contrasted-threshold:  150 !default;
// Color of text on light backgrounds
$yiq-text-dark:             $gray-900 !default;
// Color of text on dark backgrounds
$yiq-text-light:            $white !default;

/** Body **********************************************************************/
$body-bg:      $white !default;
$body-color:   $gray-900 !default;
$focus-color:  rgba($orange-kinops, 0.25) !default; // Custom

/** Links *********************************************************************/
$link-color:             $blue-lake !default;
$link-decoration:        none !default;
$link-hover-color:       darken($link-color, 15%) !default;
$link-hover-decoration:  underline !default;

/** Spacing *******************************************************************/
$spacer:             1rem !default;
$grid-gutter-width:  $spacer * 1.5 !default;

/** Components ****************************************************************/
$component-active-color:  $gray-900 !default;
$component-active-bg:     $gray-100 !default;
$border-color:            $gray-400 !default;
$border-radius:           0.25rem !default;
$border-radius-lg:        0.375rem !default;
$border-radius-sm:        0.1875rem !default;

/** Header - Custom ***********************************************************/
$header-bg:            $white !default; // Custom
$header-color:         $gray-700 !default; // Custom
$header-hover-color:   $primary !default; // Custom
$header-border-color:  $border-color !default; // Custom
$header-badge-bg:      $orange-kinops !default; // Custom
$header-badge-color:   color-yiq($orange-kinops) !default; // Custom

/** Typography ****************************************************************/
$font-family-sans-serif:  'Open Sans', sans-serif !default;
$font-family-monospace:   Menlo, Monaco, Consolas, 'Courier New', monospace !default;
$font-family-base:        $font-family-sans-serif !default;

$font-size-base:  1rem !default;
$font-size-lg:    $font-size-base * 1.25 !default;
$font-size-sm:    $font-size-base * .875 !default;

$line-height-base:  1.5 !default;
$line-height-lg:    1.5 !default;
$line-height-sm:    1.5 !default;

$h1-font-size:  $font-size-base * 2 !default;
$h2-font-size:  $font-size-base * 1.75 !default;
$h3-font-size:  $font-size-base * 1.5625 !default;
$h4-font-size:  $font-size-base * 1.375 !default;
$h5-font-size:  $font-size-base * 1.25 !default;
$h6-font-size:  $font-size-base * 1.125 !default;

$headings-margin-bottom:  $spacer !default;
$headings-font-weight:    600 !default;

/** Buttons *******************************************************************/
$input-btn-padding-y:  0.40625rem !default;
$input-btn-padding-x:  0.75rem !default;
$input-btn-font-size:  0.875rem !default;

$input-btn-padding-y-sm:  0.125rem !default;
$input-btn-padding-x-sm:  0.5rem !default;
$input-btn-font-size-sm:  0.75rem !default;

/** Tooltips ******************************************************************/
$tooltip-font-size:  $font-size-base * .75 !default;

/** Tables ********************************************************************/
$table-cell-padding:     0.5rem !default;
$table-cell-padding-sm:  0.25rem !default;

/** Nav ***********************************************************************/
$nav-tabs-link-border-width:         2px !default; // Custom
$nav-tabs-link-hover-border-color:   transparent !default;
$nav-tabs-link-active-color:         $gray-900 !default;
$nav-tabs-link-active-bg:            transparent !default;
$nav-tabs-link-active-border-color:  $secondary !default;

/** Forms *********************************************************************/
$label-margin-bottom:      0.1875rem !default; // Custom
$input-placeholder-color:  $gray-500 !default; // Custom

$form-padding:             1rem !default; // Custom
$form-bg:                  $gray-100 !default; // Custom
$form-bg-light:            $gray-100 !default; // Custom
$form-border:              1px solid $border-color !default; // Custom
$form-border-radius:       $border-radius * 1.5 !default; // Custom
$form-shadow-enabled:      false; // Custom

/** Alerts ********************************************************************/
$alert-bg-level:          -11 !default;
$alert-border-level:      -9 !default;
$alert-color-level:       7 !default;
$alert-bar-bg-level:      -11 !default; // Custom
$alert-bar-border-level:  0 !default; // Custom
$alert-bar-color-level:   7 !default; // Custom

/** Badges ********************************************************************/
$badge-muted-bg-level:     -10 !default; // Custom
$badge-muted-color-level:  8 !default; // Custom

/** Code **********************************************************************/
$code-color:  rgb(88, 110, 117);

/** Cards *********************************************************************/
$card-border-width:     0 !default;
$card-bg:               $gray-100 !default;
$card-spacing:          1.5rem !default; // Custom
$card-content-padding:  1.5rem !default; // Custom
$card-bar-color:        $primary !default; // Custom
$card-bar-sizes: (
  xs: 0.625rem,
  sm: 1.125rem,
  md: 1.75rem,
  lg: 2.5rem,
) !default; // Custom
$card-bar-icon-sizes: (
  xs: 1.0625rem,
  sm: 1.625rem,
  md: 2.375rem,
  lg: 3.25rem,
) !default; // Custom
$card-shadow-enabled:   false; // Custom

/** Hero **********************************************************************/
$hero-bg:                     $primary !default; // Custom
$hero-color:                  color-yiq($hero-bg) !default; // Custom
$hero-overlay:                rgba($primary, 0.85) !default; // Custom
$hero-meta-color:             rgba($hero-color, 0.8) !default; // Custom

