/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
 *   Layout ~ Discussions
 *~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

.discussion__content {
  overflow-y: auto;
}
.discussion--list__header {
  flex: 0 0 auto;

  .header {
    padding: 0.75rem 1rem;
    background-color: $white;
    border-bottom: 1px solid $gray16;
    color: darken($orange-kinops, 14);
    font-size: 0.9375rem;
    font-weight: 600;
    letter-spacing: 1px;
    display: flex;
    justify-content: space-between;
    h1 {
      font-size: 0.9375rem;
      font-weight: 600;
      letter-spacing: 1px;
      padding: 0;
      margin: 0;
      width: 100%;
      line-height: 1.5;
    }
    .btn-group {
      margin: -0.75rem -1rem -0.75rem 0;
      flex-shrink: 0;

      .btn {
        font-size: 1rem;
        padding: 0.5625rem 0.75rem;
        border-radius: 0;
        border: none;
      }
    }

    .search-query {
      overflow-wrap: break-word;
      word-wrap: break-word;
    }
  }

  .subheader {
    padding: 0.5rem 0.5rem 0;
    border-bottom: 1px solid $gray16;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .btn {
      padding: 0 0.5rem;
    }

    form {
      background-color: transparent;
      padding: 0;
      flex: 1;
      min-width: 10rem;

      input {
        height: 1.875rem;
        padding: 0.25rem 0.5rem;
        font-size: 0.875rem;
        line-height: 1.375rem;
      }
      .btn {
        padding: 0 1rem;
      }
    }

    > * {
      margin: 0 0.5rem 0.5rem;
    }
  }
}

.pagination-bar {
  background-color: $gray3;
  border-top: 1px solid $gray16;
  text-align: center;
  padding: 0.5rem 1rem;
  button {
    font-size: 1rem;
  }
}
