@import '../../assets/styles/variables';

.dashboard {
  .chart-wrapper {
    width: 100%;
    height: 80%;
  }

  #dashboard-grid {
    grid-template-columns: 3fr 2fr;
    gap: 1rem;
    min-height: 90%;
  }

  #dashboard__card--datagrid {
    height: 500;
  }

  #dashboard-col-report {
    justify-content: space-between;
    gap: 5%;
  }

  #dashboard-col-report > * {
    flex: auto;
  }
}
