/**
 * Tables - Common
 ******************************************************************************/

.table {
  font-size: 0.9375rem;

  thead {
    &.sortable {
      th:not(.sort-disabled) {
        cursor: pointer;
        position: relative;
        padding-right: 1.1rem;

        &:before {
          font-family: 'FontAwesome';
          font-weight: 400;
          content: '\f0dc';
          position: absolute;
          right: 0.3rem;
          opacity: 0.3;
        }

        &.sort-asc:before,
        &.sort-desc:before {
          opacity: 0.8;
          color: $primary;
        }
        &.sort-asc:before {
          content: '\f0de';
        }
        &.sort-desc:before {
          content: '\f0dd';
        }
      }
    }
  }

  tbody {
    tr {
      td {
        > a {
          font-weight: 600;
        }
        > small {
          color: $text-muted;
        }
      }
    }
  }

  .dropdown {
    .dropdown-menu {
      .dropdown-item {
        font-size: 0.9375rem;
        padding: 0.25rem 1rem;
      }
    }
  }

  .btn {
    white-space: nowrap;
  }

  &.table-sm {
    font-size: 0.875rem;

    .dropdown {
      .dropdown-menu {
        .dropdown-item {
          font-size: 0.875rem;
        }
      }
    }
  }

  &.table-draggable {
    border-collapse: separate;
    border-spacing: 0px;

    tbody {
      tr {
        &.dragging {
          background-color: $tertiary40;
          td {
            border: none;
          }
          td + td {
            display: none;
          }
        }
      }
    }
  }
}

/* Attachment files
 *-------------------------------------- */
.form-group {
  .file-table {
    display: table;
    margin: 0px;
    padding: 0px;
    .file-header {
      display: table-header-group;
      font-weight: 700;
      span {
        border-bottom: 1px solid $border-color;
      }
    }
    .file-container {
      display: table-row-group;
    }
    .file-header,
    .file-container {
      .file-row {
        display: table-row;
        > span {
          display: table-cell;
          padding: 0.5rem;
          > a {
            padding: 0 0.5rem;
          }
        }
      }
    }
  }

  &.empty {
    .file-table {
      .file-header {
        display: none;
      }
    }
  }
}

.scroll-wrapper-h {
  overflow-x: auto;
}
