/**
 * Alerts - Common
 ******************************************************************************/

.alert-bar {
  border-width: 0 0 0 0.375rem;
  border-color: transparent;
  @include border-radius(0 $alert-border-radius $alert-border-radius 0);
}

@each $color, $value in $theme-colors {
  .alert-bar.alert-#{$color} {
    color: theme-color-level($color, $alert-bar-color-level);
    @include gradient-bg(theme-color-level($color, $alert-bar-bg-level));
    border-color: theme-color-level($color, $alert-bar-border-level);
  }
}
