/**
 * Filters - Common
 ******************************************************************************/

.filter-menu {
  width: calc(100% - 3rem);
  max-width: 35rem;
  form {
    padding: 1rem;
  }
  h3,
  .filter-title {
    font-size: 1.5rem;
  }
  .form-check-fields {
    flex: 0 0 50%;
    max-width: 50%;
  }
  .form-check-label {
    font-weight: 400;
    line-height: 1.5rem;
  }
  &--timeframe {
    min-width: 240px;
  }
  .submission-filters button {
    margin-bottom: 0;
    font-size: 0.75rem;
  }
  &--large {
    min-width: 800px;
  }
}

.filter-labels {
  display: block;
  margin-top: -0.636363636em; // 7/11px = .636363636em
  padding: 0.636363636em 0.545454545em;
  font-size: 11px;
  line-height: 1;
  list-style: none;
  background-color: #f7f7f7;
  border-bottom: 1px solid #eee;

  > * {
    margin: 0.5em;
  }

  li {
    float: left;
  }

  .label {
    padding: 0.2em 0.6em 0.3em 0;
    font-size: 10.5px;
  }

  .fa {
    margin: 0 0.3em;
  }

  .label-default {
    background-color: $gray;
  }
}

.filter-pills {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  > .input-group {
    margin: 0 0 0.5rem 0.5rem;
    flex: 0 1 auto;
    width: auto;
    &.input-group-sm {
      > .input-group-prepend,
      > .input-group-append {
        > .btn,
        > .input-group-text {
          padding: 0.125rem 0.5rem;
          font-size: 0.75rem;
        }
      }
    }
  }
}
