/**
 * Header - Common
 ******************************************************************************/

.app-header {
  .navbar {
    padding: 0;
    background-color: $header-bg;
    color: $header-color;
    border-bottom: 1px solid $header-border-color;
    flex-direction: column;
    align-items: stretch;
    margin-bottom: -1px;
    z-index: 1024;

    // Deprecated - use new layout with navbar-nav
    > .nav-header {
      justify-content: space-between;

      > .nav-item-right {
        margin-left: auto;
        display: flex;
      }
    }

    .navbar-nav {
      flex-direction: row;
    }

    .dropdown {
      display: flex;

      .dropdown-menu {
        border-radius: 0;
        border: none;
        box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
        margin-top: 0;
        z-index: 9999;
        &:after {
          content: '▲';
          transform: scaleX(2);
          color: white;
          text-shadow: 0 -2px 2px rgba(0, 0, 0, 0.125);
          left: 1.5rem;
          margin-left: -8px;
          position: absolute;
          top: -16px;
          z-index: 9998;
        }
        &.alerts-menu:after,
        &.profile-menu:after {
          left: calc(100% - 1.5rem);
        }
        .dropdown-item {
          padding: 0.625rem 1rem;
          .fa {
            margin-right: 0.5rem;
          }
        }
      }
    }

    .nav-item {
      display: flex;

      &--border-left {
        border-left: 1px solid $header-border-color;
      }
      &--border-right {
        border-right: 1px solid $header-border-color;
      }
    }

    .nav-link {
      border: 0;
      background-color: transparent;
      font-size: 1rem;
      color: $header-color;
      line-height: 1.5rem;
      padding: 0.75rem;
      min-height: 3rem;
      min-width: 3rem;
      text-align: center;
      &:hover {
        color: $header-hover-color;
      }

      .badge {
        background-color: $header-badge-bg;
        border-radius: 50%;
        color: $header-badge-color;
        font-size: 0.625rem;
        height: 1.25rem;
        width: 1.25rem;
        line-height: 1.25rem;
        font-weight: 600;
        position: absolute;
        right: 5px;
        top: 5px;
        padding: 0;
      }
    }

    .main-nav-dropdown,
     // Deprecated - use above class instead
     #header-kapp-dropdown {
      .nav-link {
        color: $header-color;
        max-width: calc(13rem - 2px);
        display: flex;
        justify-content: space-between;
        &:not(.nav-link--static) {
          &:hover {
            color: $header-hover-color;
            box-shadow: 0 1px 3px 0 rgba(60, 64, 67, 0.3),
              0 2px 12px 3px rgba(60, 64, 67, 0.15);
          }
        }

        > span {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          text-transform: capitalize;
        }
        > .fa {
          line-height: 1.5rem;
          margin-left: 0.25rem;
        }
      }

      .dropdown-header {
        display: flex;
        justify-content: space-between;
        padding: 1rem 0 0.25rem;

        &:first-child {
          padding-top: 0;
        }

        &:before,
        &:after {
          content: '';
          position: relative;
          border-top: 1px solid $header-border-color;
          top: 0.5rem;
        }
        &:before {
          flex: 0 0 1.5rem;
          margin-right: 0.5rem;
        }
        &:after {
          flex: 1 1 auto;
          margin-left: 0.5rem;
        }
      }

      .dropdown-item {
        color: $gray-800;

        &.active,
        &:active {
          color: $dropdown-link-active-color;
        }
      }
    }
  }
}
