@import '../../assets/styles/_variables.scss';

header {
  height: 2.6rem;
}

.navbar-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.subheader {
  height: 2rem;
  width: 100vw;
  background-color: $color-white;
  box-shadow: 0px 4px 12px #dbdbdb;
  z-index: 100;
}

#vteams-title {
  margin: auto 10px;
  font-size: 1.2rem;
  font-weight: $weight-medium;
  color: $color-white;
}
