/**
 * Navs - Common
 ******************************************************************************/

// Nav Tabs
.nav-tabs {
  margin-bottom: 1.5rem;
  border-color: $border-color;

  .nav-link {
    border-width: 0 0 $nav-tabs-link-border-width 0;
    background: transparent;
    font-weight: 600;
    color: $gray-600;
    transition: border-color 0.3s;

    &:hover {
      border-color: darken($border-color, 5%);
    }

    &.active {
      pointer-events: none;
      cursor: default;
      color: $body-color;
    }

    .fa {
      margin-right: 0.375rem;
    }

    .badge {
      margin-left: 0.375rem;
      position: relative;
      top: -2px;
      font-size: 0.6875rem;
    }
  }
}

.nav-tabs--vertical {
  flex: 0 0 14rem;
  margin: 0;
  display: flex;
  flex-direction: column;
  border-bottom: none;
  align-self: flex-start;

  .nav-link {
    font-size: 0.875rem;
    padding: 0.75rem 1rem;
    border-width: 0 $nav-tabs-link-border-width * 1.5 0 0;
    border-radius: $border-radius 0 0 $border-radius;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &.active {
      background-color: $gray-100;
    }

    .badge {
      top: 0;
      margin-left: auto;
    }
  }

  .nav-item--header {
    font-size: 0.875rem;
    text-transform: uppercase;
    color: $primary;
    padding: 0.25rem 1rem;
    margin-top: 1.5rem;
    &:first-child {
      margin-top: 0.5rem;
    }
  }
}

@media (min-width: 1400px) {
  .nav-tabs__side-container {
    display: flex;
    .nav-tabs {
      flex: 0 0 14rem;
      margin: 0 3rem 0 0;
      display: flex;
      flex-direction: column;
      border-bottom: none;
      align-self: flex-start;

      .nav-link {
        font-size: 0.875rem;
        padding: 0.75rem 1rem;
        border-width: 0 $nav-tabs-link-border-width * 1.5 0 0;
        border-radius: $border-radius 0 0 $border-radius;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &.active {
          background-color: $gray-100;
        }

        .badge {
          top: 0;
        }
      }

      + * {
        flex: 1 1 auto;
      }
    }
  }
}

// Return Nav
.nav-return {
  display: block;
  border-bottom: 1px solid $border-color;
  font-size: 0.875rem;
  padding: 0.8125rem 1rem 0.75rem;
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
  .icon {
    margin-right: 0.5em;
  }
  .sidebar & {
    background-color: $white;
  }
}
