/**
 * Cards - Common
 ******************************************************************************/

.card {
  color: $body-color;
  @if $card-shadow-enabled {
    box-shadow: 0rem 0.1875rem 0.5rem 0rem rgba(0, 0, 0, 0.075);
  }
  border-radius: $border-radius;

  // Card bar
  > .card__bar {
    flex: 0 0 auto;
    display: flex;
    justify-content: center;
    border: 0px solid $card-bar-color;
    color: darken($card-bar-color, 5%);

    .card__bar-icon {
      position: relative;
      border-radius: 50%;
      background-color: $card-bg;
      text-align: center;
      width: map-get($card-bar-icon-sizes, 'md');
      height: map-get($card-bar-icon-sizes, 'md');
      font-size: map-get($card-bar-icon-sizes, 'md') * 0.6;
      line-height: map-get($card-bar-icon-sizes, 'md');
    }

    .card__bar-img {
      position: relative;
      border-radius: 50%;
      background-color: $card-bg;
      text-align: center;
    }

    // Card bar sizes (e.g. card__bar--sm)
    @each $size in ('xs', 'sm', 'lg') {
      &--#{$size} {
        .card__bar-icon {
          width: map-get($card-bar-icon-sizes, $size);
          height: map-get($card-bar-icon-sizes, $size);
          font-size: map-get($card-bar-icon-sizes, $size) * 0.6;
          line-height: map-get($card-bar-icon-sizes, $size);
        }
      }
    }

    // Card bar colors (e.g. card__bar--primary)
    @each $color, $value in $theme-colors {
      &--#{$color} {
        border-color: $value;
        color: darken($value, 5%);

        &.card__bar--muted {
          border-color: theme-color-level($color, -3);
        }
      }
    }
  }

  // Card bar - top-specific
  &:not(.card--left-bar) {
    > .card__bar {
      border-top-width: map-get($card-bar-sizes, 'md');
      border-top-left-radius: inherit;
      border-top-right-radius: inherit;

      .card__bar-icon,
      .card__bar-img {
        top: map-get($card-bar-icon-sizes, 'md') / -2;
        margin-bottom: map-get($card-bar-icon-sizes, 'md') / -2;
        @if $card-shadow-enabled {
          box-shadow: 0rem 0.125rem 0.25rem 0rem rgba(0, 0, 0, 0.15);
        }
      }

      // Card bar sizes (e.g. card__bar--sm)
      @each $size in ('xs', 'sm', 'lg') {
        &--#{$size} {
          border-top-width: map-get($card-bar-sizes, $size);

          .card__bar-icon,
          .card__bar-img {
            top: map-get($card-bar-icon-sizes, $size) / -2;
            margin-bottom: map-get($card-bar-icon-sizes, $size) / -2;
          }
        }
      }
    }
  }

  // Card bar - left-specific
  &.card--left-bar {
    flex-direction: row;
    > .card__bar {
      flex-direction: column;
      border-left-width: map-get($card-bar-sizes, 'md');
      border-bottom-left-radius: inherit;
      border-top-left-radius: inherit;

      .card__bar-icon {
        left: map-get($card-bar-icon-sizes, 'md') / -2;
        margin-right: map-get($card-bar-icon-sizes, 'md') / -2;
        margin-top: map-get($card-bar-icon-sizes, 'md') * 0.25;
        margin-bottom: map-get($card-bar-icon-sizes, 'md') * 0.25;
        @if $card-shadow-enabled {
          box-shadow: 0.125rem 0rem 0.25rem 0rem rgba(0, 0, 0, 0.15);
        }
      }

      // Card bar sizes (e.g. card__bar--sm)
      @each $size in ('xs', 'sm', 'lg') {
        &--#{$size} {
          border-left-width: map-get($card-bar-sizes, $size);

          .card__bar-icon {
            left: map-get($card-bar-icon-sizes, $size) / -2;
            margin-right: map-get($card-bar-icon-sizes, $size) / -2;
            margin-top: map-get($card-bar-icon-sizes, $size) * 0.25;
            margin-bottom: map-get($card-bar-icon-sizes, $size) * 0.25;
          }
        }
      }
    }
  }

  &.card--form {
    box-shadow: none;
    border: 1px solid $border-color;
    border-radius: $border-radius;
  }

  > .card__col,
  > .card__row {
    padding: $card-content-padding;
  }

  > .card__col + .card__col {
    border-top: 1px solid $border-color;
  }

  .card__col {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    font-size: 0.875rem;
    line-height: 1.5;
    font-weight: 400;

    &--center {
      .card__row {
        justify-content: center;
        text-align: center;
      }
    }

    &--middle {
      justify-content: center;
    }
  }

  .card__row {
    flex: 0 0 auto;
    display: flex;
    justify-content: flex-start;
    font-size: 0.875rem;
    line-height: 1.5;
    font-weight: 400;

    &--center {
      justify-content: center;
      text-align: center;
    }

    strong {
      font-weight: 600;
    }

    > *:not(:last-child) {
      margin-right: 0.25rem;
    }

    + .card__row:not(:empty) {
      padding-top: 0.5rem;
    }

    &--prepend,
    &--append {
      > * {
        align-self: flex-start;
      }
    }

    &--prepend {
      margin-right: $card-content-padding !important;
    }

    &--append {
      margin-left: $card-content-padding !important;
    }

    &--icon {
      flex: 0 0 auto;
      font-size: 1.75rem;
      display: flex;
      justify-content: center;
    }

    &--multi {
      flex-wrap: wrap;

      > * {
        flex: 0 1 auto;
        align-self: center;

        &.badge {
          margin-left: 0;
        }

        &:not(:last-child) {
          margin-right: $card-content-padding;
        }
        + .card__row:not(:empty) {
          padding-top: 0;
        }
      }
      > .badge {
        margin-top: 0.125rem;
        margin-bottom: 0.125rem;
      }
    }
  }

  > .card__row {
    flex: 1 1 auto;
    align-items: center;
  }

  .card__col,
  .card__row {
    > .fa-rounded {
      flex: 0 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $card-bar-color;
      border-radius: 50%;
      color: color-yiq($card-bar-color);
      font-size: 0.9375rem;
      flex-basis: 1.5625rem;
      height: 1.5625rem;
      width: 1.5625rem;
    }

    > .badge {
      flex: 0 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 0.8125rem;
      font-weight: 600;
      padding: 0.25rem 0.75rem;
      height: 1.3125rem;
      &.badge-stylized {
        padding: 0.25rem 0;
      }
    }

    &--title {
      font-size: 1.25rem;
      line-height: 1.25;
      font-weight: 600;
      > * {
        flex: 0 1 auto;
        align-self: center;
      }
      > .badge {
        margin-top: 0.125rem;
        margin-left: auto;
      }
      > .toggle {
        margin-top: 0.125rem;
        margin-left: auto;
      }
    }

    &--subtitle {
      font-size: 1rem;
      font-weight: 600;
    }

    &--meta {
      font-size: 0.8125rem;

      dl {
        flex: 1 1 auto;
        display: flex;
        flex-wrap: wrap;
        margin: 0 -0.75rem -0.5rem;

        > div {
          flex: 1 0 auto;
          margin: 0 0.75rem;
        }

        dt {
          white-space: nowrap;
        }

        &.stacked {
          margin: 0;
          display: table;

          > div {
            display: table-row;
          }

          dt {
            display: table-cell;
            padding-right: 1.5rem;
            width: 1%;
          }

          dd {
            display: table-cell;
          }
        }

        &.flat {
          > div {
            flex: 0 0 auto;
            margin-bottom: 0.25rem;
          }

          dt {
            display: inline-block;
            margin-right: 0.25rem;
          }
          dd {
            display: inline-block;
            margin-bottom: 0;
          }
        }
      }

      p {
        white-space: pre-wrap;
        margin-top: 0;
        + p {
          margin-top: 0.5rem;
        }
      }
    }
  }

  .card__corner {
    position: absolute;
    right: 0;
    top: 0;
    width: 3.5rem;
    height: 3.5rem;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    padding: 0.25rem;
    flex-direction: row;
    border-radius: 0 $border-radius;
    border: none;
    background: linear-gradient(-135deg, $dark 50%, transparent 50%);
    clip-path: polygon(0 0, 100% 0, 100% 100%);
    color: $light;
    transition: clip-path 0.2s, width 0.2s, height 0.2s, opacity 0.2s;

    .fa {
      text-align: center;
      font-size: 1.25rem;
      width: 1.25em;
      margin-top: 0.125rem;
    }

    &--actionable {
      &.card__corner--active {
        color: $warning;
      }

      &:hover {
        width: 4rem;
        height: 4rem;
      }

      &.card__corner--hover:not(.card__corner--active) {
        clip-path: polygon(100% 0, 100% 0, 100% 0);
        opacity: 0;
        @at-root .card:hover .card__corner--hover:not(.card__corner--active) {
          clip-path: polygon(0 0, 100% 0, 100% 100%);
          opacity: 1;
        }
      }
    }

    @each $color, $value in $theme-colors {
      &--#{$color} {
        background: linear-gradient(-135deg, $value 50%, transparent 50%);
        color: color-yiq($value, $dark, $light);

        &.card__corner--active {
          color: color-yiq($value, $blue-lake, $warning);
        }
      }
    }
  }

  // Card link hover
  @at-root a#{&} {
    @include hover-focus() {
      text-decoration: none;
      background-color: darken($card-bg, 2%);
    }
  }
  // Card colors  (e.g. card--primary)
  @each $color, $value in $theme-colors {
    &--#{$color} {
      background-color: $value;
      color: color-yiq($value);
      @at-root a#{&} {
        @include hover-focus() {
          background-color: darken($value, 3%);
          color: color-yiq($value);
        }
      }
      .text-muted {
        color: color-yiq(
          $value,
          $dark: $gray-600,
          $light: $gray-500
        ) !important;
      }
    }
  }
  &--white {
    background-color: $white;
    color: color-yiq($white);
    @at-root a#{&} {
      @include hover-focus() {
        background-color: darken($white, 3%);
        color: color-yiq($white);
      }
    }
  }
}
a.card {
  @include hover-focus() {
    text-decoration: none;
    background-color: darken($card-bg, 2%);
  }
}
@each $color, $value in $theme-colors {
  a.card--#{$color} {
    @include hover-focus() {
      background-color: darken($value, 3%);
      color: color-yiq($value);
    }
  }
}
// Cards container with even spacing
.cards {
  display: flex;
  flex-direction: column;
  padding: 0 0 $card-spacing;
  margin: 0;
  list-style: none;
  .card {
    flex: 0 0 auto;
    margin-bottom: $card-spacing;
  }
  &--seconds,
  &--thirds,
  &--fourths {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &--seconds {
    .card {
      flex: 0 0 auto;
      @media all and (min-width: 0) {
        flex-basis: 100%;
        @media all and (-ms-high-contrast: none) {
          width: 100%;
          flex-basis: auto;
        }
      }
      @media all and (min-width: 576px) {
        flex-basis: calc(50% - #{$card-spacing} / 2);
        @media all and (-ms-high-contrast: none) {
          width: calc(50% - #{$card-spacing} / 2);
          flex-basis: auto;
        }
      }
    }
  }
  &--thirds {
    .card {
      flex: 0 0 auto;
      @media all and (min-width: 0) {
        flex-basis: 100%;
        @media all and (-ms-high-contrast: none) {
          width: 100%;
          flex-basis: auto;
        }
      }
      @media all and (min-width: 576px) {
        flex-basis: calc(50% - #{$card-spacing} / 2);
        @media all and (-ms-high-contrast: none) {
          width: calc(50% - #{$card-spacing} / 2);
          flex-basis: auto;
        }
      }
      @media all and (min-width: 992px) {
        flex-basis: calc(33.33333333% - #{$card-spacing} * 2 / 3);
        @media all and (-ms-high-contrast: none) {
          width: calc(33.33333333% - #{$card-spacing} * 2 / 3);
          flex-basis: auto;
        }
      }
    }
    @media all and (min-width: 992px) {
      &:after {
        content: ' ';
        flex: 0 0 calc(33.33333333% - #{$card-spacing} * 2 / 3);
      }
      @media all and (-ms-high-contrast: none) {
        &:after {
          flex-basis: auto;
          width: calc(33.33333333% - #{$card-spacing} * 2 / 3);
        }
      }
    }
  }
  &--fourths {
    .card {
      flex: 0 0 auto;
      @media all and (min-width: 0) {
        flex-basis: 100%;
        @media all and (-ms-high-contrast: none) {
          width: 100%;
          flex-basis: auto;
        }
      }
      @media all and (min-width: 576px) {
        flex-basis: calc(50% - #{$card-spacing} / 2);
        @media all and (-ms-high-contrast: none) {
          width: calc(50% - #{$card-spacing} / 2);
          flex-basis: auto;
        }
      }
      @media all and (min-width: 992px) {
        flex-basis: calc(33.33333333% - #{$card-spacing} * 2 / 3);
        @media all and (-ms-high-contrast: none) {
          width: calc(33.33333333% - #{$card-spacing} * 2 / 3);
          flex-basis: auto;
        }
      }
      @media all and (min-width: 1200px) {
        flex-basis: calc(25% - #{$card-spacing} * 3 / 4);
        @media all and (-ms-high-contrast: none) {
          width: calc(25% - #{$card-spacing} * 3 / 4);
          flex-basis: auto;
        }
      }
    }
    @media all and (min-width: 992px) {
      &:after {
        content: ' ';
        flex: 0 0 calc(33.33333333% - #{$card-spacing} * 2 / 3);
      }
      @media all and (-ms-high-contrast: none) {
        &:after {
          flex-basis: auto;
          width: calc(33.33333333% - #{$card-spacing} * 2 / 3);
        }
      }
    }
    @media all and (min-width: 1200px) {
      &:after,
      &:before {
        content: ' ';
        flex: 0 0 calc(25% - #{$card-spacing} * 3 / 4);
      }
      &:before {
        order: 1;
      }
      @media all and (-ms-high-contrast: none) {
        &:after,
        &:before {
          flex-basis: auto;
          width: calc(25% - #{$card-spacing} * 3 / 4);
        }
      }
    }
  }
}
.dropdown-menu--card {
  padding: 0;
  border: none;
  border-radius: $border-radius;
  .card {
    @if $card-shadow-enabled {
      box-shadow: 0rem 0.1875rem 0.5rem 0rem rgba(0, 0, 0, 0.3);
    }
    margin: 0;
  }
}
